.assortmentBeerSliderWrap {
  width: 940px;
  margin: 0 auto;
  padding-top: 50px;
  margin-bottom: -25px;
}
@media only screen and (max-width: 940px) {
  .assortmentBeerSliderWrap {
    width: auto;
    margin-left: 78px;
  }
}
@media only screen and (max-width: 750px) {
  .assortmentBeerSliderWrap {
    width: auto;
    margin-left: 40px;
  }
}
.assortmentBeerSlider {
  width: 132%;
  height: 315px;
  box: horizontal middle;
  position: relative;
  right: 24px;
  padding-left: 20px;
}
@media only screen and (max-width: 940px) {
  .assortmentBeerSlider {
    width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .assortmentBeerSlider {
    padding-left: 10px;
  }
}
.assortmentSliderItems {
  width: calc(100% - 83px);
  margin: 0;
}
.beersliderItem {
  height: 315px;
  padding: 0 20px;
  box: vertical middle center;
}
.assortmentSliderItems.swiper-container-autoheight .swiper-wrapper {
  -webkit-box-align: end;
  -ms-flex-align: end;
  -webkit-align-items: flex-end;
  align-items: flex-end;
}
