.header {
  width: 100%;
  box: horizontal;
  max-width: 980px;
  position: absolute;
  padding: 65px 78px;
  z-index: 10;
}
@media only screen and (max-width: 880px) {
  .header {
    padding: 65px 40px;
  }
}
@media only screen and (max-width: 750px) {
  .header {
    position: fixed;
    padding: 0px 40px;
    height: 70px;
    box: horizontal middle space-between flex-start row-reverse;
    flex-direction: row-reverse !important;
    background-color: #ffd427;
  }
}
.headerMenu {
  width: 64%;
  box: horizontal space-around middle;
}
.headerMenu svg {
  display: none;
}
@media only screen and (max-width: 980px) {
  .headerMenu {
    width: 70%;
  }
}
@media only screen and (max-width: 880px) {
  .headerMenu {
    box: horizontal space-between middle;
  }
}
@media only screen and (max-width: 750px) {
  .headerMenu {
    height: 17px;
    width: 25px;
  }
  .headerMenu svg {
    display: block;
    height: 17px;
    width: 25px;
  }
}
.headerMenuWrap {
  box: horizontal space-around middle;
  width: 100%;
}
@media only screen and (max-width: 880px) {
  .headerMenuWrap {
    box: horizontal space-between middle;
  }
}
@media only screen and (max-width: 750px) {
  .headerMenuWrap {
    display: none;
    position: absolute;
    flex-direction: column !important;
    justify-content: flex-start !important;
    align-items: stretch !important;
    left: 0px;
    top: 70px;
    padding: 10px 40px 30px 40px;
    background-color: #ffd427;
  }
}
@media only screen and (max-width: 750px) {
  .headerMenuItems:hover {
    color: #fff !important;
  }
}
.headerMenuItems {
  color: #fff;
  font-family: 'Muller-Black';
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  text-decoration: none;
  transition: 0.3s all ease-in-out;
}
.headerMenuItems:hover {
  color: #ffd427;
}
@media only screen and (max-width: 750px) {
  .headerMenuItems:hover {
    color: #fff !important;
  }
}
@media only screen and (max-width: 750px) {
  .headerMenuItems {
    float: left;
    width: 100%;
    padding: 22px 10px;
    color: #000;
    font-size: 20px;
    text-transform: none;
    border-top: 2px solid #e5be23;
  }
  .headerMenuItems:hover {
    color: #fff;
  }
  .headerMenuItems:first-child {
    border-top: none;
  }
}
.headerMenuItemActive {
  color: #ffd427 !important;
}
@media only screen and (max-width: 750px) {
  .headerMenuItemActive {
    color: #000 !important;
  }
  .headerMenuItemActive:hover {
    color: #fff !important;
  }
}
.logo {
  width: 164px;
  height: 30px;
  margin: 0px 43px 0px 0px;
}
@media only screen and (max-width: 700px) {
  .logo {
    margin: 0px 43px 0px 0px;
  }
}
@media only screen and (max-width: 750px) {
  .logo {
    margin: 0;
    width: 129px;
    height: 24px;
  }
}
.headerPhoneIco svg {
  height: 20px;
  width: 20px;
  display: none;
}
@media only screen and (max-width: 750px) {
  .headerPhoneIco svg {
    display: block;
  }
}
.headerMenuNear {
  display: none;
  position: absolute;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  top: 348px;
  z-index: 11;
  height: 332px;
  background-size: cover;
  background-image: url("../img/headerMenuBg.jpg");
  padding: 35px 52px;
}
.headerMenuNear +retina {
  background-image: url("../img/headerMenuBg@2x.jpg");
}
.headerMenuNear:before {
  background-color: rgba(0,0,0,0.7);
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.headerMenuNear .headerMenuNearWrap {
  margin-bottom: 5px;
}
.headerMenuNear p {
  color: #fff;
  color: #ffd427;
  font-family: 'Muller-Bold';
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  position: relative;
}
.headerMenuNear .headerMenuNearTextRight {
  position: relative;
  right: 0px;
  color: #fff;
  margin-top: 0px;
  text-align: right;
  font-family: 'Muller-Regular';
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}
.headerMenuNear .typeOfMarket {
  size: 140px 22px;
  margin-bottom: 15px;
  margin-top: 5px;
  background-color: #e0cec5;
  border-radius: 100px;
  color: #383838;
  font-family: 'Muller-Bold';
  font-size: 14px;
  font-weight: 700;
  line-height: 17px;
  letter-spacing: -0.31px;
  padding: 3px 15px;
  position: relative;
}
.headerMenuNear .headerMenuNearTextWhite {
  color: #fff;
}
.headerMenuNear .headerMenuNearHowToFind {
  box: middle center horizontal;
  margin-top: 12px;
}
.headerMenuNear .headerMenuNearHowToFindIcon svg {
  size: 28px 38px;
  position: relative;
  margin-right: 10px;
}
