.mainPageWelcomeBlock {
  height: 100vh;
  width: 100%;
  background-image: url("../img/bg.png");
  background-color: #000;
  background-size: cover;
  overflow: hidden;
}
.mainPageWelcomeBlock h1 {
  font-size: 77px;
  font-weight: 900;
  color: #fff;
  text-align: center;
  margin-top: 220px;
  line-height: 70px;
  font-family: 'Muller-Black';
}
.mainPageWelcomeBlock h1 span {
  color: #ffd427;
}
.mainPageWelcomeBlock p {
  font-size: 20px;
  line-height: 28px;
  color: #fff;
  width: 464px;
  margin: 10px auto;
  text-align: center;
  font-family: 'Muller-Regular';
}
@media only screen and (max-width: 510px) {
  .mainPageWelcomeBlock p {
    width: 100%;
  }
}
