.footer.footer {
  position: relative;
}
.footerBottom {
  height: 85px;
  background: #121212;
  padding: 0 170px 0 80px;
  box: middle flex-start horizontal;
  position: relative;
}
@media only screen and (max-width: 1020px) {
  .footerBottom {
    padding: 0 105px 0 80px;
  }
}
@media only screen and (max-width: 750px) {
  .footerBottom {
    background-image: url("../img/bottomtexture.png");
    padding: 0 40px;
  }
}
@media only screen and (max-width: 580px) {
  .footerBottom {
    box: none;
    height: 100px;
    display: block !important;
  }
}
.footerBottomLeft {
  box: middle flex-start horizontal;
}
@media only screen and (max-width: 580px) {
  .footerBottomLeft {
    box: none;
    float: left;
    padding-top: 25px;
    display: block !important;
  }
}
@media only screen and (max-width: 321px) {
  .footerBottomLeft {
    width: 100%;
    text-align: center;
  }
}
.footerCopyright {
  font-size: 9px;
  color: #4a4a4a;
  font-family: 'Muller-Regular';
  width: 160px;
  letter-spacing: -0.2px;
  line-height: 12px;
}
@media only screen and (max-width: 321px) {
  .footerCopyright {
    float: none;
    width: 160px;
    text-align: center;
    margin: 0 auto;
  }
}
.footerWarning {
  margin-left: 47px;
}
@media only screen and (max-width: 1020px) {
  .footerWarning {
    display: none;
  }
}
.footerWarning p {
  width: 247px;
  font-size: 10px;
  color: #4a4a4a;
  font-family: 'Muller-Regular';
  margin-left: 10px;
  float: left;
  letter-spacing: -0.2px;
  line-height: 14px;
  padding-top: 5px;
}
.warningIcon {
  size: 38px;
  border-radius: 50%;
  background-image: url("../img/18.png");
  margin-right: 10px;
  float: left;
}
.footerSocialIcons {
  width: 145px;
  margin-left: 47px;
  box: middle horizontal space-between;
}
@media only screen and (max-width: 580px) {
  .footerSocialIcons {
    justify-content: center !important;
    top: -40px;
    margin-left: -40px;
    width: 100%;
    position: absolute;
  }
}
.footerSocialIcons a {
  size: 38px;
  box: vertical middle center;
  background-color: #383838;
  border-radius: 50%;
}
@media only screen and (max-width: 580px) {
  .footerSocialIcons a {
    margin: 0 8px !important;
  }
}
.footerSocialIcons a svg {
  flex: 0 1 auto;
  size: 14px;
  fill: #b7b7b7;
  transition: all 0.3s;
}
.footerSocialIcons a:hover svg {
  fill: #fff;
  size: 18px;
}
.footerSocialDevelop {
  font-size: 10px;
  color: #4a4a4a;
  font-family: 'Muller-Regular';
  position: absolute;
  right: 80px;
}
@media only screen and (max-width: 750px) {
  .footerSocialDevelop {
    right: 40px;
  }
}
@media only screen and (max-width: 580px) {
  .footerSocialDevelop {
    top: 25px;
  }
}
@media only screen and (max-width: 321px) {
  .footerSocialDevelop {
    left: 0;
    float: left;
  }
}
@media only screen and (max-width: 321px) {
  .footerSocialDevelop {
    position: relative;
    width: 100%;
    left: 0;
    text-align: center;
    top: 10px;
  }
}
.footerSocialDevelop a {
  color: #ffd427;
  border-bottom: 1px solid #ffd427;
  text-decoration: none;
  padding-bottom: 2px;
}
.footerSocialDevelop a:hover {
  border-bottom: none;
}
.footerTop {
  box: middle horizontal space-between;
  padding: 0px 85px;
  background-image: url("../img/bottomtexture.png");
  height: 142px;
}
.footerTop +retina {
  background-image: url("../img/bottomtexture-2x.png");
}
@media only screen and (max-width: 995px) {
  .footerTop {
    justify-content: center !important;
  }
}
@media only screen and (max-width: 560px) {
  .footerTop {
    padding: 0px 40px;
  }
}
.footerMenu {
  width: 76%;
  margin-right: 9%;
  box: middle horizontal space-between;
}
@media only screen and (max-width: 1060px) {
  .footerMenu {
    width: 76%;
    margin-right: 7%;
  }
}
@media only screen and (max-width: 995px) {
  .footerMenu {
    display: none;
  }
}
.footerMenuItem a {
  color: #6e6e6e;
  font-size: 12px;
  font-family: "Muller-Black";
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.8;
  position: relative;
  transition: 0.3s all ease-in-out;
}
.footerMenuItem a:hover {
  color: #fff;
}
.footerMenuItemActive a {
  color: #fff;
}
.footerMenuItemActive a:before {
  content: '';
  position: absolute;
  height: 5px;
  width: 20px;
  top: -23px;
  background: #ffd427;
}
