a.mask {
  background: rgba(0,0,0,0.7);
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  overflow: hidden;
  top: 0;
  left: 0;
  box: vertical middle center;
}
.mask {
  display: none;
}
/*section,header,footer,.newsMain
    filter: blur(3px) grayscale(20%)*/
.popUpAgeWindow {
  position: relative;
  z-index: 111111111111;
  size: 940px 484px;
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0 0 40px #000;
  box: vertical center;
  padding-top: 70px;
}
@media only screen and (max-width: 1040px) {
  .popUpAgeWindow {
    width: 90%;
    padding: 0px 40px;
    padding-top: 70px;
  }
}
@media only screen and (max-width: 650px) {
  .popUpAgeWindow {
    height: 550px;
  }
}
@media only screen and (max-width: 600px) {
  .popUpAgeWindow {
    width: 100%;
    height: 100%;
    border-radius: 0;
    padding-top: 120px;
  }
}
.popUpAgeLogo .logo-age {
  size: 197px 36px;
  margin-bottom: 31px;
}
@media only screen and (max-width: 600px) {
  .popUpAgeLogo .logo-age {
    margin-bottom: 0px;
  }
}
.popUpAgeButtons {
  width: 560px;
  margin-top: 10px;
  box: horizontal middle center;
  flex-wrap: wrap;
}
@media only screen and (max-width: 740px) {
  .popUpAgeButtons {
    width: 482px;
  }
}
@media only screen and (max-width: 650px) {
  .popUpAgeButtons {
    width: 230px;
  }
}
.popUpAgeButton {
  background: #e0cec5;
  size: 270px 60px;
  box: vertical center middle;
  border-radius: 100px;
  box-shadow: 1px 1px 0 #8f8f8f;
  font-family: "Muller-Bold";
  font-size: 20px;
  cursor: pointer;
  transition: 0.1s all ease-in-out;
}
.popUpAgeButton:hover {
  box-shadow: none;
}
.popUpAgeButton:nth-child(2) {
  margin-left: 20px;
}
@media only screen and (max-width: 650px) {
  .popUpAgeButton:nth-child(2) {
    margin-top: 20px;
    margin-left: 0;
  }
}
@media only screen and (max-width: 740px) {
  .popUpAgeButton {
    size: 230px 56px;
    font-size: 18px;
  }
}
.popUpAgeButtonYes {
  background: #ffd427;
}
.popUpAgeHeader h2 {
  margin: 31px 0px;
  font-size: 24px;
  font-family: "Muller-Bold";
  width: 560px;
  text-align: center;
  line-height: 29px;
  letter-spacing: -0.07px;
}
@media only screen and (max-width: 740px) {
  .popUpAgeHeader h2 {
    width: 100%;
    font-size: 22px;
    line-height: 26px;
  }
}
.popUpAgeWarning {
  box: horizontal center middle;
  margin-top: 50px;
}
@media only screen and (max-width: 600px) {
  .popUpAgeWarning {
    margin-top: 80px;
  }
}
.popUpAgeWarning .warningLabel {
  display: none;
}
@media only screen and (max-width: 790px) {
  .popUpAgeWarning .warningLabel {
    display: flex;
  }
}
@media only screen and (max-width: 980px) {
  .popUpAgeWarning .warniingLabelDesctop {
    height: 30px;
    width: 550px;
  }
}
@media only screen and (max-width: 790px) {
  .popUpAgeWarning .warniingLabelDesctop {
    display: none;
  }
}
.popUpAgeWarning svg {
  size: 688px 33px;
  color: #a7a7a7;
  font-family: "PFDinTextCompPro";
  font-size: 33px;
  font-weight: 300;
  letter-spacing: -0.09px;
  line-height: 33px;
}
@media only screen and (max-width: 980px) {
  .popUpAgeWarning svg {
    font-size: 26px;
  }
}
@media only screen and (max-width: 800px) {
  .popUpAgeWarning svg {
    width: 70%;
  }
}
@media only screen and (max-width: 700px) {
  .popUpAgeWarning svg {
    font-size: 22px;
    line-height: 20px;
  }
}
@media only screen and (max-width: 450px) {
  .popUpAgeWarning svg {
    font-size: 17px;
  }
}
.popUpAgeBitMap {
  size: 74px;
  background: url("../img/bitmap.png");
  margin-right: 18px;
}
@media only screen and (max-width: 980px) {
  .popUpAgeBitMap {
    size: 52px;
    background-size: cover;
  }
}
@media only screen and (max-width: 790px) {
  .popUpAgeBitMap {
    margin-right: 0px;
  }
}
@media only screen and (max-width: 540px) {
  .popUpAgeBitMap {
    size: 37px;
  }
}
@media only screen and (max-width: 430px) {
  .popUpAgeBitMap {
    margin-right: 10px;
  }
}
