.netMarketsToggle {
  size: 301px 42px;
  border: 1px solid #ffd427;
  border-radius: 100px;
  margin: 0px auto;
  margin-top: 30px;
  margin-bottom: 0px;
  padding: 3px;
  position: relative;
  transition: 0.4s all;
  display: none;
}
@media only screen and (max-width: 750px) {
  .netMarketsToggle {
    display: block;
    box: horizontal middle space-between;
  }
}
.netMarketsToggle p {
  position: relative;
  z-index: 2;
  color: #1e1e1e;
  font-family: 'Muller-Bold';
  font-size: 18px;
  font-weight: 700;
  letter-spacing: -0.09px;
  line-height: 22px;
  margin-right: 30px;
  margin-left: 30px;
  cursor: pointer;
}
.netMarketsToggle p.netMarketsToggleActive {
  cursor: default;
}
.netMarketsToggleSwich {
  border-radius: 100px;
  background: #ffd427;
  position: absolute;
  width: 50%;
  top: 3px;
  bottom: 3px;
  transition: 0.4s all;
}
