@reset-global pc;
* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -webkit-overflow-scrolling: none;
}
a:focus {
  outline: 0;
}
html {
  height: 100%;
  min-width: 320px;
}
body {
  position: relative;
  min-height: 100%;
  border: 24 solid #ffd427;
}
@media only screen and (max-width: 750px) {
  body {
    border: none;
  }
}
.main {
  background-color: #f7f7f7;
  padding-top: 3000px;
}
.mainHeaderForSimplePages {
  color: #090909;
  font-family: "Muller-Black";
  font-size: 40px;
  font-weight: 900;
  letter-spacing: -0.11px;
  line-height: 40px;
  margin-bottom: 35px;
}
.pageWrap {
  width: 940px;
  margin: 150px auto;
  margin-bottom: 0px;
  overflow: hidden;
}
@media only screen and (max-width: 1165px) {
  .pageWrap {
    width: 760px;
  }
}
@media only screen and (max-width: 980px) {
  .pageWrap {
    margin-left: 78px;
    margin-right: 78px;
    width: auto;
  }
}
@media only screen and (max-width: 880px) {
  .pageWrap {
    margin-left: 40px;
    margin-right: 40px;
  }
}
