.newsMain {
  height: 210px;
  width: 100%;
  box: horizontal middle;
}
@media only screen and (max-width: 640px) {
  .newsMain {
    flex-flow: row wrap;
    height: auto;
  }
}
.newsMainItem {
  height: 100%;
  width: 33.3333%;
  background-size: 150%;
  background-position: center;
  text-decoration: none;
  padding: 0px 45px;
  position: relative;
  overflow: hidden;
}
.newsMainItem:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  z-index: 1;
  transition: 0.3s all;
}
.newsMainItem:hover .mainNewsItemImage {
  transform: scale(1.3);
}
.newsMainItem:hover:before {
  background: rgba(0,0,0,0.4);
}
.newsMainItem:hover .newsMainItemIcon {
  margin-right: -10px;
  background-color: #ffd427;
}
@media only screen and (max-width: 1180px) {
  .newsMainItem {
    padding: 0 25px;
  }
}
@media only screen and (max-width: 980px) {
  .newsMainItem {
    width: 50%;
  }
  .newsMainItem:last-child {
    display: none;
    width: 0;
  }
}
@media only screen and (max-width: 640px) {
  .newsMainItem {
    flex: 1 100%;
    width: 100%;
    height: 164px;
  }
}
.newsMainItemsRowItem {
  box: horizontal middle space-between;
  margin-top: 125px;
  position: relative;
  z-index: 10;
}
@media only screen and (max-width: 640px) {
  .newsMainItemsRowItem {
    margin-top: 95px;
  }
}
.newsMainItemHeader {
  font-family: "Muller-Bold";
  color: #fff;
  font-size: 22px;
  width: 80%;
}
@media only screen and (max-width: 1060px) {
  .newsMainItemHeader {
    font-size: 18px;
  }
}
.newsMainItemIcon {
  size: 43px;
  border-radius: 50%;
  background-color: #fff;
  box: vertical middle center;
  transition: 0.4s all;
  margin-right: 25px;
}
.newsMainItemIcon svg {
  size: 24px;
}
.mainNewsItemImage {
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  position: absolute;
  transition: 0.3s all;
  background-size: 150%;
  background-position: center;
}
