.assortment {
  overflow: hidden;
}
.assortimentHeaderWrap {
  box: horizontal start;
}
.assortmentCategoryHeader {
  color: #000;
  font-family: "Muller-Black";
  font-size: 18px;
  font-weight: 900;
  letter-spacing: 5px;
  line-height: 22px;
  text-transform: uppercase;
  margin-right: 100px;
  min-width: 273px;
}
@media only screen and (max-width: 1165px) {
  .assortmentCategoryHeader {
    margin-right: 20px;
  }
}
.assortimentItemCategory {
  box: horizontal space-between;
}
@media only screen and (max-width: 720px) {
  .assortimentItemCategory {
    box: vertical start;
  }
}
.assortmentItem {
  margin-top: 30px;
}
@media only screen and (max-width: 720px) {
  .assortmentItem {
    margin-top: 0;
  }
}
.assortmentCategoryDescrip {
  color: #5d5d5d;
  font-family: 'Muller-Regular';
  font-size: 18px;
  letter-spacing: -0.05px;
  line-height: 24px;
  margin-right: 30px;
}
@media only screen and (max-width: 720px) {
  .assortmentCategoryDescrip {
    margin-top: 15px;
  }
}
select.seda {
  display: none;
}
.assortmentBeerItems {
  overflow: hidden;
  padding: 47px 0;
  box: start wrap;
}
.assortmentContainer {
  perspective: 1000;
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 34px;
}
.assortmentContainer:hover .assortmentCard {
  transform: rotateY(180deg);
}
.assortmentContainer,
.assortmentCardItem {
  position: relative;
  backface-visibility: hidden;
}
.assortmentCardBack {
  width: 270px;
  right: 7px;
  top: 0;
  border-radius: 10px;
  padding: 30px;
  box-sizing: border-box;
  transform: rotateY(180deg);
  background: #fff;
  position: absolute;
  box-shadow: 0 0 20px 3px rgba(0,0,0,0.1);
}
.assortmentCardBack h2 {
  text-align: center;
  padding-bottom: 2px;
  font-family: "Muller-Bold";
  font-size: 22px;
  color: #222;
  letter-spacing: -0.16px;
  line-height: 19px;
}
.assortmentCardBack .assortmentCardDescriptionTxt {
  color: #4c4c4c;
  font-family: 'Muller-Regular';
  font-size: 16px;
  font-weight: 400;
  text-align: left;
  letter-spacing: -0.06px;
  margin-top: 23px;
  line-height: 20px;
}
.assortmentCardBack .assortmentCardDescHeader {
  font-size: 12px;
  color: #746963;
  line-height: 14px;
  text-align: center;
  margin-top: 0;
  font-family: 'Muller-Regular';
  letter-spacing: -0.09px;
}
.assortmentCardBack .assortmentCardDescriptionItems {
  margin-top: 16px;
}
.assortmentCardBack .assortmentCardDescriptionItems:before {
  content: '';
  height: 2px;
  right: 15px;
  margin-top: 10px;
  z-index: 9;
  left: 15px;
  position: absolute;
  background: #e0cec5;
}
.assortmentCard {
  width: 277px;
  height: 342px;
  background: #fff;
  border-radius: 10px;
  padding: 32px;
  box-shadow: 0 0 30px 8px rgba(0,0,0,0.05);
  position: relative;
  transform-style: preserve-3d;
  transition: 0.5s;
  backface-visibility: hidden;
}
.assortmentCardFront {
  z-index: 10;
}
.assortmentCardFront h2 {
  font-family: "Muller-Black";
  font-size: 24px;
  color: #222;
  margin-top: 10px;
  letter-spacing: -0.16px;
}
.assortmentCardIcon svg {
  size: 24px 22px;
  position: absolute;
  top: 0;
  right: 0;
}
.assortmentCardImage {
  size: 170px;
  margin: 0 auto;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.assortmentCardDescriptionItems {
  margin-top: 28px;
  width: 100%;
  box: start;
}
.assortmentCardDescriptionItem {
  background: #e0cec5;
  border-radius: 30px;
  padding: 5px 10px 2px;
  margin-right: 10px;
  float: left;
  position: relative;
  z-index: 10;
  font-family: "Muller-Bold";
  font-size: 14px;
  color: #333;
}
.assortmentCardInWrap {
  box: middle center space-between;
  margin-top: 10px;
}
.assortmentCardLinkText {
  color: #000;
  font-family: 'Muller-Medium';
  font-size: 12px;
  line-height: 14px;
  font-style: italic;
  letter-spacing: -0.19px;
}
.assortmentCardLinkText span {
  font-family: 'Muller-Bold';
}
.assortmentCardLinkCircle {
  size: 36px;
  box: vertical middle center;
  background: #ffd427;
  border-radius: 50%;
}
.assortmentCardLinkCircle svg {
  size: 20px;
}
.assortmentCardTagNew {
  size: 72px 24px;
  background-color: #f5220f;
  border-radius: 100px;
  color: #e2e2e2;
  font-family: 'Muller-Bold';
  box-sizing: border-box;
  padding: 5px 10px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: -0.03px;
  line-height: 14px;
  position: absolute;
  top: -9px;
  left: 0;
}
.assortmentCardTagNewSlider {
  top: inherit;
  bottom: -50px;
  left: 57px;
}
