.contacts {
  background: #eaeaea;
}
#map {
  height: 795px;
  width: 100%;
}
@media only screen and (max-width: 750px) {
  #map {
    height: 375px;
  }
}
.headerMenuContacts .headerMenuItems {
  color: #333 !important;
}
.headerMenuContacts .headerMenuItems:hover {
  color: #ffd427 !important;
}
.contactItems {
  size: 324px 626px;
  background-color: #fff;
  border-radius: 10px;
  position: absolute;
  z-index: 9;
  top: 125px;
  right: 100px;
  padding: 35px;
  box: start vertical;
  box-shadow: 10px 10px 20px rgba(0,0,0,0.1);
}
@media only screen and (max-width: 750px) {
  .contactItems {
    top: 0;
    left: 0;
    height: auto;
    width: 100%;
    position: relative;
    padding-top: 120px;
    border-radius: 0;
    box-shadow: none;
  }
}
.contactItems p {
  font-family: 'Muller-Regular', Arial;
  line-height: 24px;
  margin-top: 8px;
  letter-spacing: 0.08px;
  font-size: 16px;
}
.contactItems p a {
  color: #000;
  text-decoration: none;
  padding-bottom: 4px;
  border-bottom: 1px solid #ffd427;
  transition: 0.3s all ease-in-out;
}
.contactItems p :hover {
  border-bottom: 1px solid #fff;
}
.contactsHeaderMain {
  font-size: 34px;
  font-family: 'Muller-Black';
  padding-bottom: 15px;
}
.contactsHeader {
  font-size: 16px;
  font-family: 'Muller-Bold';
  margin-top: 19px;
}
.contactItemAdress {
  width: auto;
}
@media only screen and (max-width: 750px) {
  .contactItemAdress {
    order: 10;
    margin-top: 20px;
  }
}
